.cost {
    padding: 40px 20px 20px;
    background-color: $grey;

    & h2 {
        @include heading-2;
    }

    & p, span {
        @include plain-text;
    }

    @media (min-width: $desktop-width) {
        grid-row: 9/10;
        grid-column: 1/3;
        padding: 20px 40px 20px;
        border-top: 100px solid transparent;
	    margin-top: -100px;
        background-clip: padding-box;
    }
}